import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Toast } from 'react-bootstrap';
import { Dispatcher } from '../../types/store';
import { clearSuccessAlert } from '../../store/ErrorProvider/actions';
import { ErrorHandlerState } from '../../types/Error';
import CheckMark from '../../assets/CheckMark.component.svg';
import CloseIcon from '../../assets/CloseIcon.component.svg';
import styles from './styles.module.scss';

const Alert = (): React.ReactElement => {
  const dispatch = useDispatch<Dispatcher>();
  const [show, setShow] = useState<boolean>(true);
  const [alertMessage, setAlertMessage] = useState<string | null>(null);

  const { successAlert } = useSelector<{
    errorHandler: ErrorHandlerState;
  }, ErrorHandlerState>((state) => state.errorHandler);

  useEffect(() => {
    if (successAlert) {
      setAlertMessage(successAlert);
      setShow(true);
      dispatch(clearSuccessAlert());
    }
  }, [successAlert]);
  return (
    <Toast
      show={show}
      className={styles['success-alert-message']}
      // delay={3000}
      // onClose={() => setShow(false)}
      // autohide
      animation
    >
      <Toast.Body>
        <div>
          <CheckMark />
          <span>
            {`Успех! ${alertMessage}`}
          </span>
        </div>
        <button type="button" onClick={() => setShow(false)}>
          <CloseIcon />
        </button>
      </Toast.Body>
    </Toast>
  );
};

export default Alert;
